import React, { Dispatch, SetStateAction } from 'react'
import { Typography } from '@mui/material'
import { SPaper, SAppIcon } from './AppsMenu.styles';
import { IServiceProvider } from '../ProductContainer/ProductContainer';
import PopperMenu from '../PopperMenu';
import { RoundedContainer } from '../PopperMenu/PopperMenu.styles';

type Props = {
  apps: IServiceProvider[] | undefined
  open: boolean
  anchorEl: HTMLElement | null,
  setAnchorEl: Dispatch<SetStateAction<HTMLElement | null>>,
  isLoading: boolean
}

const AppsMenu = (props: Props) => {
  const { anchorEl, setAnchorEl, open, isLoading, apps } = props
  const id = open ? "apps-menu" : undefined

  return (
    <PopperMenu
      id={id}
      open={open}
      anchorEl={anchorEl}
      setAnchorEl={setAnchorEl}
      isLoading={isLoading}
      WrapperComponent={RoundedContainer}
    >
      <>
        <SPaper>
          {apps?.map(
            ({ id, name, display_name, is_accessible, image, host_url }) => (
              <SAppIcon
                key={name}
                target="_blank"
                href={`http://${host_url}`}
                underline='none'
                tabIndex={0}
              >
                <img
                  style={{ maxWidth: "40%", flex: 1 }}
                  src={`${process.env.REACT_APP_SERVER_URL}/${image}`}
                  alt={name}
                  aria-hidden={true}
                />

                <Typography
                  sx={{
                    maxWidth: "100%",
                    overflow: "hidden",
                    whiteSpace: "nowrap",
                    textOverflow: "ellipsis",
                    textAlign: "center",
                    color: "gray"
                  }}
                  color="inherit"
                  variant="caption"
                  fontWeight={900}
                >
                  {display_name}
                </Typography>
              </SAppIcon>
            )
          )}
        </SPaper>
      </>
    </PopperMenu>
  )
}

export default AppsMenu